main {
    display: block;
    width: 100%;
    min-height: 75vh;
    padding: 10px;
    max-width: 50rem;
    margin: 6.250rem auto 0 auto;
}

@supports (grid-area: auto) {

    @media screen and (min-width: 50rem) {

        main {
            max-width: 100%;
            margin: 2rem auto;
        }

    }
}

